				'И верно — было на что поглядеть: чёрный был весь в панцире — от кончиков усов до кончика хвоста. Гладкие латы охватывали его грудь и спину. Из-под твёрдого забрала на тоненьких стебельках высовывались два неподвижных глаза. Длинные прямые усы торчали вперёд как пики. Четыре пары тонких ног были как вилочки, две клешни — как две зубастые пасти.',
				'Никто из прудовых жителей ещё ни разу в жизни не видал рака, и все из любопытства лезли поближе к нему. Рак шевельнулся — все испугались и отодвинулись подальше. Рак поднял переднюю ножку, ухватил вилкой свой глаз, вытянул стебелёк и давай чистить. Это было так удивительно, что все опять полезли на рака, а один карасик даже наткнулся на его усы.',
