import * as React from 'react';
import Modal from '@mui/material/Modal';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import ReactAudioPlayer from 'react-audio-player';
import './Modal.css';

import Puzzels from './modal/Puzzels';
import Books from './modal/Books';
import Power from './modal/Power';
import CustomLinks from './modal/CustomLinks';
import About from './modal/About';
import Gallery from './modal/Gallery';
import CustomTab from './modal/CustomTab';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import AudiotrackIcon from '@mui/icons-material/Audiotrack';
import OndemandVideoIcon from '@mui/icons-material/OndemandVideo';
import YoutubeEmbed from './YoutubeEmbed';

import { VH_KOEFF, VW_KOEFF } from '../constant/animals';
import { STYLE, STYLE_2, STYLE_POWER } from '../constant/modal/style';

function CustomModal(props) {
	const [played, setPlayed] = React.useState(false);
	const [video, setVideo] = React.useState(false);
	const [pointed, setPointed] = React.useState({});

	const playVideo = (id) => {
		if (video === id) {
			setVideo(false);
		} else {
			setVideo(id);
		}
	};

	const handlePointClick = (id) => {
		if (pointed[id]) {
			setPointed({
				...pointed,
				[id]: false,
			});
		} else {
			setPointed({
				...pointed,
				[id]: true,
			});
		}
	};

	const playAudio = () => {
		if (played) {
			setPlayed(false);
		} else {
			setPlayed(true);
		}
	};

	const iconStyle = {
		color: '#fff',
		cursor: 'pointer',
		fontSize: props.compareWH ? `${VH_KOEFF * 1}vh` : `${VW_KOEFF * 1}vw`,
	};

	const closeModalWindow = (id) => {
		if(id === video) {
			setVideo(false);
		} else {
			setVideo(false);
			setPointed({});
			setPlayed(false);
			props.handleClose();
		}
	};

	const getStyle = () => {
		if (props.data.isAnimal) {
			return STYLE_2;
		}
		if (props.data.powerStyle) {
			return STYLE_POWER;
		}
		return STYLE;
	};

	const content = props.data.gallery ? <Gallery data={props.data.gallery}/> : (
			<Card sx={getStyle()} id="card" key={'card'}>
						{video && <YoutubeEmbed embedId={props.data.video} compareWH={props.compareWH}/>} 			
						{!video && props.data && props.data.isCCSTitle && <div className={props.data.class} style={{
							fontSize: '60px', //props.compareWH ? `${VH_KOEFF * 3}vh` : `${VW_KOEFF * 3}vw`,
						}}>
							{props.data.title}
						</div>}
						{!video && props.data.fullSrc && <CardMedia
							component="img"
							image={props.data && props.data.fullSrc}
							alt={props.data && props.data.alt}
						/>}
						<CardContent>
							{!video && played && (
								<ReactAudioPlayer
									src={props.data.mp3}
									autoPlay
									volume={0.2}
								/>
							)}
							{!video && pointed[props.data.src] && (
								<ReactAudioPlayer
									src={props.data.stih}
									autoPlay
									volume={0.2}
									controls
								/>
							)}
							{!video && props.data.stih && (
								<div onClick={() => handlePointClick(props.data.src)} style={{ display: 'inline', marginLeft: props.compareWH ? `${VH_KOEFF * 0.2}vh` : `${VW_KOEFF * 0.2}vw`,}}>
									<MenuBookIcon className='audio' style={{
										...iconStyle,
										color: pointed[props.data.src] ? 'black' : iconStyle.color,
									}}/>
								</div>
							)}
							{!video && props.data.mp3 && (
								<div onClick={playAudio} style={{ display: 'inline', marginLeft: props.compareWH ? `${VH_KOEFF * 0.2}vh` : `${VW_KOEFF * 0.2}vw`}}>
									<AudiotrackIcon className='audio' style={{
										...iconStyle,
										color: played ? 'black' : iconStyle.color,
									}}/>
								</div>
							)}
							{!video && props.data.video && (
								<div onClick={() => playVideo(props.data.src)} style={{ display: 'inline', marginLeft: props.compareWH ? `${VH_KOEFF * 0.2}vh` : `${VW_KOEFF * 0.2}vw`,}}>
									<OndemandVideoIcon className='audio' style={iconStyle}/>
								</div>
							)}
							{!video && pointed[props.data.src] && 
								<div style={{
									fontSize: props.compareWH ? `${VH_KOEFF * 0.5}vh` : `${VW_KOEFF * 0.5}vw`,
									color: '#fff',
									paddingLeft: props.compareWH ? `${VH_KOEFF * 0.5}vh` : `${VW_KOEFF * 0.5}vw`,
								}}><div>{props.data.stihData.autor}</div><div>"{props.data.stihData.title}"</div></div>
							}
							{!video && !props.data.hideTitle && <div className='title-text'>
								{props.data && props.data.title}
							</div>}
							{!video && props.data && props.data.tabs && props.data.tabs.length > 0 && <CustomTab data={props.data.tabs} compareWH={props.compareWH}/>}
							{!video && props.data && props.data.images && props.data.images.length > 0 && <Puzzels data={props.data.images} compareWH={props.compareWH}/>}
							{!video && props.data && props.data.books && props.data.books.length > 0 && <Books data={props.data.books} compareWH={props.compareWH}/>}
							{!video && props.data && props.data.links && props.data.links.length > 0 && <CustomLinks data={props.data.links} compareWH={props.compareWH}/>}
							{!video && props.data && props.data.paragraphs && props.data.paragraphs.length > 0 && <About data={props.data.paragraphs} compareWH={props.compareWH}/>}
							{!video && props.data && props.data.power && props.data.power.length > 0 && <Power data={props.data.power} compareWH={props.compareWH} onClose={() => closeModalWindow()}/>}
						</CardContent>
					</Card>
		);

	return (
		<Modal
			open={props.data && props.data.open}
			onClose={() => closeModalWindow(props.data.src)}
			aria-labelledby="modal-modal-title"
			aria-describedby="modal-modal-description"
		>
			{content}
		</Modal>
	);
}

export default CustomModal;