import React from 'react';

const YoutubeEmbed = ({ embedId, isStyled, marginSizeL, marginSizeT, className }) => (
  <div className={`video-responsive video-common ${className ? className : 'video-main' }`} style={isStyled ? {
    marginLeft: marginSizeL,
    marginTop: marginSizeT,
  } : {}}>
    <iframe
      width="853"
      height="480"
      src={`https:\/\/www.youtube.com\/embed\/${embedId}`}
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
      title="Embedded youtube"
    />
  </div>
);

export default YoutubeEmbed;