import * as React from 'react';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import ListItemIcon from '@mui/material/ListItemIcon';
import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';
import './ListMenu.css';

function ListMenu(props) {

	//const koeffFontSizeS1 = 0.5;
	//const koeffFontSizeS2 = 0.6;
	const koeffPaddingRightIcon = 0.1;
	const koeffListItemPaddingT = 0.1;
	const koeffListItemPaddingB = 0.1;

	const buildSize = (koeff) => {
		return props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`;
	};

	const style1 = {
		color: '#fff',
		fontSize: '16px', //buildSize(koeffFontSizeS1),
	};
	const style2 = {
		color: '#fff',
		fontSize: '18px', //buildSize(koeffFontSizeS2),
	};
	return (
		<List key={props.menu.id}> 
			{props.menu.map(item => (
				<ListItem
					id={`list-${item.id}`}
					button={!!item.modal}
					onClick={item.isClose ? props.onCloseMenu : item.modal && props.clickMenu(item.modal)} 
					key={item.id}
					sx={{
						paddingTop: buildSize(koeffListItemPaddingT),
						paddingBottom: buildSize(koeffListItemPaddingB),
						cursor: item.isClose ? 'pointer' : undefined,
					}}
				>
					{item.icon && <ListItemIcon style={{ 
						color: '#fff', 
						minWidth: 'auto', 
						paddingRight: buildSize(koeffPaddingRightIcon), 
					}}>{item.icon}</ListItemIcon>}
					<div style={item.customStyle ? style2 : style1 }>{item.title}</div>
				</ListItem>
			))}
		</List>
	);
}

export default ListMenu;