import * as React from 'react';
import { styled } from '@mui/material/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import ReactAudioPlayer from 'react-audio-player';

const CustomTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    color: '#fff',
    boxShadow: theme.shadows[1],
    fontSize: 11,
    maxWidth: 'none',
    backgroundColor: "initial",
    textTransform: 'uppercase',
    font: 'bold 2.1vw "Helvetica Neue", Helvetica, Arial, sans-serif',
  	textShadow: '0 1px 0 #cccccc, 0 1px 0 #c9c9c9, 0 1px 0 #bbbbbb, 0 1px 0 #b9b9b9, 0 1px 0 #aaaaaa, 0 1px 1px rgb(0 0 0 / 10%), 0 0 3px rgb(0 0 0 / 10%), 0 1px 3px rgb(0 0 0 / 7%), 0 3px 1px rgb(0 0 0 / 10%), 0 1px 1px rgb(0 0 0 / 7%), 0 1px 1px rgb(0 0 0 / 7%), 0 6px 4px rgb(0 0 0 / 15%)',
  },
}));

function Box(props) {
  const [played, setPlayed] = React.useState(false);

  const playAudio = (mp3) => {
    setPlayed(played ? false : mp3);
  };

	const image = props.item.src && (<div><img className={props.item.class ? props.item.class : 'image'} src={props.item.src} alt={props.item.alt} style={props.item.img.style} /></div>);
	const audio = props.item.audio && (<div><PlayCircleIcon className='audio' style={props.item.audio.style}/></div>);
  const style = {
      display: 'inline-block',
      color: 'white',
      lineHeight: 0,
      position: 'absolute',
      ...props.item.style,
	};
	return (
	<CustomTooltip title={props.item.title ? props.item.title : " "} placement="right-start">
		<div className='box' style={style} onClick={props.item.audio ? () => playAudio(props.item.audio.audio) : () => props.clickFunc(props.item.modal)}>
			{image}
      {audio}
			{props.item.showTitle && <div className='box-text' style={props.item.text.style}>{props.item.title}</div>}
      {played && (
        <ReactAudioPlayer
          src={played}
          autoPlay
          volume={0.2}
        />
      )}
		</div>
	</CustomTooltip>
	);
}

export default Box;