import React from 'react';
import './Power.css';
import ReactAudioPlayer from 'react-audio-player';

import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import HomeIcon from '@mui/icons-material/Home';
import CloseIcon from '@mui/icons-material/Close';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import MusicOffIcon from '@mui/icons-material/MusicOff';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import ImageIcon from '@mui/icons-material/Image';
import PlayCircleFilledIcon from '@mui/icons-material/PlayCircleFilled';
import YoutubeEmbed from '../YoutubeEmbed';

import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';
import { VIDEO, TEXT, IMAGE, TITUL } from '../../constant/menu/powerpoint/types';

function Power(props) {

	const [slidered, setSlidered] = React.useState(0);
	const [answered, setAnswered] = React.useState(false);
	const [played, setPlayed] = React.useState({ 
		array: props.data[0].sounds,
		current: 0,
		play: !!props.data[0].sounds 
	});

	const [started, setStarted] = React.useState(false);

	const [showed, setShowed] = React.useState(false);
	const [booked, setBooked] = React.useState(false);

	const playAudio = () => {
		if (started) {
			setStarted(false);
		} else {
			setStarted(true);
		}
	};

	const showBook = () => {
		if (booked) {
			setBooked(false);
		} else {
			setBooked(true);
		}
	};

	const playNext = () => {
		if (played.current === played.array.length - 1) {
			setStarted(false);
		} else {
			setPlayed({
				...played,
				current: played.current + 1,
			});
		}
		setShowed(true);
	};

	const handleChangeSlide = (id) => {
		setStarted(false);
		setBooked(false);
		setShowed(false);		
		setSlidered(id);
		setPlayed({
			array: props.data[id].sounds,
			current: 0,
			play: !!props.data[id].sounds,
		});
	};

	const handleAnsweredClick = (id) => {
		if (answered === id) {
			setAnswered(false);
		} else {
			setAnswered(id);
		}
	};

	const koeffMainBox = 10;
	const koeffH = 12.3;
	const koeffW = 16;
	const koeffT = 0.6;
	const koeffL = 2.4;
	const koeffA = 2;
	const koeffPaddingL = 0.5;
	const koeffPaddingTtitle = 3;
	const koeffFontTitul = 0.7;
	const koeffImgW = 12;
	const koeffNumberT = 11.4;
	const koeffNumberL = 18.1;
	const koefPlayMarginT = -3;
	const koefPlayAudioT = 10;
	const koefPlayAudioL = 21;
	const koefBookT = 10;
	const koefBookL = 23;
	const koefPlayAudioFont = 1.5;
	const defaultColor = '#04455B';
	const showAutoPlay = false;

	const buildSize = (koeff) => {
		return props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`;
	};

	const audioIcon = started ? 
		<MusicOffIcon onClick={playAudio} id={'audio-icon'} style={{
			position: 'absolute',
			top: buildSize(koefPlayAudioT),
			left: buildSize(koefPlayAudioL),
			fontSize: buildSize(koefPlayAudioFont),
			cursor: 'pointer',
			color: 'white',
		}} /> : 
		<MusicNoteIcon onClick={playAudio} id={'audio-icon'} style={{
			position: 'absolute',
			top: buildSize(koefPlayAudioT),
			left: buildSize(koefPlayAudioL),
			fontSize: buildSize(koefPlayAudioFont),
			cursor: 'pointer',
			color: 'white',
		}} />;

	const bookIcon = booked ? <ImageIcon onClick={showBook} id={'book-icon'} style={{
			position: 'absolute',
			top: buildSize(koefBookT),
			left: buildSize(koefBookL),
			fontSize: buildSize(koefPlayAudioFont),
			cursor: 'pointer',
			color: 'white',
		}} /> : <MenuBookIcon onClick={showBook} id={'book-icon'} style={{
			position: 'absolute',
			top: buildSize(koefBookT),
			left: buildSize(koefBookL),
			fontSize: buildSize(koefPlayAudioFont),
			cursor: 'pointer',
			color: 'white',
		}} />;

	const getImage = (data) => {
		const content = data.map(item => {
			const imgKoeff = item.koeffSize ? item.koeffSize : koeffImgW;
			const showImg = item.showAfterVoice ? showed : true;
			if (props.data[slidered].isBook) {
				return !booked && <img
					key={item.id}
					src={item.img}
					alt={item.id}
					style={{
						width: buildSize(imgKoeff),
						marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
						marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
						borderRadius: !!item.borderRadius ? item.borderRadius : 0,
					}}
				/>;
			}
			if (item.img2) {
				if (showImg) {
					return <img
						key={item.id}
						src={item.img}
						alt={item.id}
						style={{
							width: buildSize(imgKoeff),
							marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
							marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
							borderRadius: !!item.borderRadius ? item.borderRadius : 0,
						}}
					/>;
				} else {
					return <img
						key={item.id}
						src={item.img2}
						alt={item.id}
						onClick={playAudio}
						style={{
							width: buildSize(imgKoeff),
							marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
							marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
							borderRadius: !!item.borderRadius ? item.borderRadius : 0,
							cursor: 'pointer',
						}}
					/>;
				}
			}
			return showImg && <img
				key={item.id}
				src={item.img}
				alt={item.id}
				style={{
					width: buildSize(imgKoeff),
					marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
					marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
					borderRadius: !!item.borderRadius ? item.borderRadius : 0,
				}}
			/>;
		});
		return content;
	};

	const getText = (data) => {
		return data.map(item => {
			if (item.embedId) {
				return getVideo(item);
			}
			if (item.img) {
				return getImage([item]);
			}
			if (item.book) {
				return booked && <div key={item.id} style={{ 
					width: '95%', 
					wordBreak: 'break-word',
					fontSize: item.koeffFontSize ? buildSize(item.koeffFontSize) : undefined,
					textAlign: item.center ? 'center' : 'justify',
					fontWeight: item.fontWeight ? 'bold' : undefined,
					marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
					marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
					color: item.color ? item.color : defaultColor,
					textIndent: buildSize(0.7),
				}}>{item.book}</div>;
			}
			if (item.answer) {
				const radius = buildSize(0.2);
				return (
					<div 
						key={item.id}
						onClick={() => handleAnsweredClick(item.id)}
						style={{ 
							width: '93%', 
							wordBreak: 'break-word',
							color: defaultColor,
							fontSize: item.koeffFontSize ? buildSize(item.koeffFontSize) : undefined,
							textAlign: item.center ? 'center' : 'justify',
							fontWeight: item.fontWeight ? 'bold' : undefined,
							marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : buildSize(0.1),
							marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
							cursor: 'pointer',
							borderStyle: 'solid',
							borderRadius: `${radius} ${radius} ${radius} ${radius}`,
							paddingLeft: buildSize(0.2),
						}}
					>{item.text} {answered === item.id && <div style={{ color: 'green', fontWeight: 'bold', }}>{item.answer}</div>}</div>
				);
			}
			const showText = item.showAfterVoice ? showed : true;
			return showText && <div key={item.id} style={{ 
				width: '95%', 
				wordBreak: 'break-word',
				fontSize: item.koeffFontSize ? buildSize(item.koeffFontSize) : undefined,
				textAlign: item.center ? 'center' : 'justify',
				fontWeight: item.fontWeight ? 'bold' : undefined,
				marginTop: item.koeffMarginT ? buildSize(item.koeffMarginT) : 0,
				marginLeft: item.koeffMarginL ? buildSize(item.koeffMarginL) : 0,
				color: item.color ? item.color : defaultColor,
				textIndent: buildSize(0.7),
			}}>{item.text}</div>;
		});
	};

	const getVideo = (data) => {
		return <YoutubeEmbed 
			embedId={data.embedId} 
			compareWH={props.compareWH}
			isStyled={data.isStyled}
			marginSizeL={buildSize(data.koeffMarginL)}
			marginSizeT={buildSize(data.koeffMarginT)}
			width={data.width}
			className={data.className}
		/>;
	};

	const getTitul = (data) => {
		return data.map((item, index) => (
			<div key={item.id} style={index === 0 ? {
				paddingTop: buildSize(koeffPaddingTtitle),
				textAlign: 'center',
				fontSize: item.koeffFontTitul ? buildSize(item.koeffFontTitul) : buildSize(koeffFontTitul),
				fontWeight: 'bold',
				color: defaultColor,
			} : { 
				textAlign: 'center',
				fontSize: item.koeffFontTitul ? buildSize(item.koeffFontTitul) : buildSize(koeffFontTitul),
				fontWeight: 'bold',
				color: defaultColor,
			}}>{item.text}</div>
		));
	};

	const getByType = (type, data) => {
		switch(type) {
			case TEXT:
				return getText(data);
			case TITUL:
				return getTitul(data);
			case IMAGE:
				return getImage(data);
			case VIDEO:
				return getVideo(data);
			default:
				return null;
		}
	};

	const content = getByType(props.data[slidered].type, props.data[slidered].content);

	return (
		<div className='grid-container'>
			<div className='box1' onClick={slidered === 0 ? undefined :() => handleChangeSlide(0)}>
				{slidered !== 0 && <HomeIcon className='icon' sx={{ 
					color: 'white', 
					fontSize: buildSize(koeffA),
				}} />}
			</div>
			<div className='box2' onClick={slidered - 1 < 0 ? undefined : () => handleChangeSlide(slidered - 1)}>
				<ArrowBackIosIcon className='icon' sx={{ 
					color: 'white', 
					fontSize: buildSize(koeffA),
					paddingLeft: buildSize(koeffPaddingL), 
					opacity: slidered - 1 < 0 ? '0!important' : '100%',
				}} />
			</div>
			<div className='box3'></div>
			<div style={{
				height: buildSize(koeffH),
				width: buildSize(koeffW),
				background: props.data[slidered].background ? `no-repeat url("${props.data[slidered].background}")` : undefined,
				backgroundSize: '100%',
				display: 'inline-block',
			}} className='box4'>
				<div style={{
					textAlign: 'left',
					paddingLeft: buildSize(koeffL),
					paddingTop: buildSize(koeffT),
					overflow: props.data[slidered].isSource ? 'auto' : undefined,
					height: props.data[slidered].isSource ? buildSize(koeffMainBox) : undefined,				
				}}>
					{content}
					<div style={{
						position: 'absolute',
						top: buildSize(koeffNumberT),
						left: buildSize(koeffNumberL),
					}}>{slidered + 1} / {props.data.length}</div>
				</div>
			</div>

			<div className='box5' onClick={props.onClose}>
				<CloseIcon className='icon' sx={{ 
					color: 'white', 
					fontSize: buildSize(koeffA),
				}} />
			</div>
			<div className='box6' onClick={slidered + 1 > props.data.length - 1 ? undefined : () => handleChangeSlide(slidered + 1)}>
				<ArrowForwardIosIcon className='icon' sx={{ 
					color: 'white', 
					fontSize: buildSize(koeffA),
					opacity: slidered + 1 > props.data.length - 1 ? '0!important' : '100%',
				}} />
			</div>
			<div className='box7'>
				{showAutoPlay && <PlayCircleFilledIcon className='icon' sx={{ 
					color: 'white', 
					fontSize: buildSize(koeffA),
					marginTop: buildSize(koefPlayMarginT),
				}} />}
				{ played.play && audioIcon}
				{ props.data[slidered].isBook && bookIcon}
			</div>
			{started && played.play && (
				<ReactAudioPlayer
					src={played.play && played.array[played.current].mp3}
					autoPlay
					volume={0.9}
					onEnded={playNext}
				/>
			)}
		</div>
	);
}
export default Power;