import * as React from 'react';
import './CustomLinks.css';
import Link from '@mui/material/Link';

import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';

function CustomLinks(props) {

	const koeff = 0.6;
	const koeff1 = 0.4;
	const koeffMargin = 0.4;

	return props.data.map(link => {
		return (
			<div className='link-box' key={link.id}>
				<div style={{ color: '#fff', fontSize: props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`}}>
					{link.title}
				</div>
				{link.texts.map((text, index) => {
					return (
						<div key={index} style={{ 
							marginLeft: props.compareWH ? `${VH_KOEFF * koeffMargin}vh` : `${VW_KOEFF * koeffMargin}vw`,
							color: '#fff', 
							fontSize: props.compareWH ? `${VH_KOEFF * koeff1}vh` : `${VW_KOEFF * koeff1}vw`,
						}}>
							{text.title ? `"${text.title}"` : ''} {text.isLink ? <Link href={text.link}>{text.link}</Link> : text.link} {text.author}
						</div>
					);
				})}
			</div>
		);
	});
}

export default CustomLinks;