import * as React from 'react';
import './About.css';
import Collapse from '@mui/material/Collapse';

import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';

function About(props) {

	const [collapsed, setCollapsed] = React.useState({});

	const handleExpandClick = (id) => {
		if (collapsed[id]) {
			setCollapsed({
				...collapsed,
				[id]: false,
			});
		} else {
			setCollapsed({
				...collapsed,
				[id]: true,
			});
		}
	};

	const koeff = 0.6;
	const koeff1 = 0.4;
	const koeffMargin = 0.4;

	return props.data.map(data => {
		if (data.type === 'collapse') {
			return (
				<div className='collapse-box'>
					<div onClick={data.collapsed ? undefined : () => handleExpandClick(data.id)}  style={{ fontSize: props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`}}>
						{data.title}
					</div>
					<Collapse in={data.collapsed ? true : collapsed[data.id]} timeout="auto" unmountOnExit>
						<div onClick={data.collapsed ? undefined : () => handleExpandClick(data.id)} className='collapse-text'>
							{data.texts.map((item, index) => {
								return (
									<div key={index} style={{ 
										fontSize: props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`,
										textAlign: 'justify',
									}}>
										{item.text}
									</div>
								);
							})}
						</div>
					</Collapse>
				</div>
			);
		} else {
			return (
				<div className='link-box' key={data.id}>
					{data.title && <div style={{ color: '#fff', fontSize: props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`}}>
						{data.title}
					</div>}
					{data.texts.map((text, index) => {
						return (
							<div key={index} style={{ 
								marginLeft: props.compareWH ? `${VH_KOEFF * koeffMargin}vh` : `${VW_KOEFF * koeffMargin}vw`,
								color: '#fff', 
								fontSize: props.compareWH ? `${VH_KOEFF * koeff1}vh` : `${VW_KOEFF * koeff1}vw`,
								textAlign: 'justify',
							}}>
								{text.text}
							</div>
						);
					})}
				</div>
			);
		}
	});
}
export default About;