import './App.css';
import React, { useLayoutEffect, useState } from 'react';
import Box from './component/Box';
import Modal from './component/Modal';
import CustomMenu from './component/CustomMenu';

import { TITLE_STYLE, SHOW_ANIMALs, ANIMALS, VH_KOEFF, VW_KOEFF, SHOW_IMAGE_TITLE, SHOW_NO_IMAGE_TITLE } from './constant/animals';

function useWindowSize() {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    function updateSize() {
      setSize([window.innerWidth, window.innerHeight]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);
  return size;
}

function App() {
  const [open, setOpen] = React.useState(false);
  const handleOpen = (modal) => setOpen(modal);
  const handleClose = () => setOpen({ open: false });
  const [width, height] = useWindowSize();
  const compareWH = width >= 1.426*height;
  const className = compareWH ? 'contentW' : 'contentH';
  const styles = compareWH ? { marginLeft: `${(width - 1.42 * height) / 2 }px` } : { marginTop: `${(height - 0.705 * width) / 2 }px` };

  const buildSize = (koeff) => {
    return compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`;
  };

  const animalsL = SHOW_ANIMALs.map(name => {
    const item = ANIMALS[name];
    const animal = {
      title: item.title,
      src: item.src,
      alt: item.alt,
      class: item.class,
      style: {
        top: buildSize(item.topKoeff),
        left: buildSize(item.leftKoeff),
      },
      img: item.alt ? {
        style: {
          height: buildSize(item.heightKoeff),
        }
      } : undefined,
      text: {
        style: {
          fontSize: buildSize(item.fontSizeKoeff),
          textAlign: 'center',
        }
      },
      modal: !item.audio ? {
        open: true,
        title: item.title,
        mp3: item.mp3,
        text: item.text,
        embedId: item.embedId,
        src: item.src,
        fullSrc: item.fullSrc,
        alt: item.alt,
        tabs: item.tabs,
        isAnimal: item.isAnimal,
        stih: item.stih,
        stihData: item.stihData,
        customStyle: item.customStyle,
        video: item.video,
      } : undefined,
      audio: item.audio ? {
        audio: item.audio,
        style: {
          fontSize: buildSize(item.heightKoeff),
          color: '#000',
          cursor: 'pointer',
        }
      } : undefined,
      showTitle: item.alt ? SHOW_IMAGE_TITLE : SHOW_NO_IMAGE_TITLE
    };

    return <Box key={name} item={animal} clickFunc={handleOpen}/>;
  });

  return (
    <div className='App'>
      <Modal data={open} handleClose={handleClose} compareWH={compareWH}/>
      <div className={className} style={styles}>
        {animalsL}
        <div className="title" style={{
          ...TITLE_STYLE,
          fontSize: buildSize(2.1),
          top: buildSize(16.7),
          left: buildSize(2.6),
          paddingLeft: buildSize(0),
          paddingRight: buildSize(0.3),
        }}>ОБИТАТЕЛИ ВОДОЁМА</div>
      </div>
      <CustomMenu compareWH={compareWH}/>
    </div>
  );
}

export default App;
