import * as React from 'react';
import { styled } from '@mui/material/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
import './Excludeincorrect.css';

import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';
import { INCORRECT, CORRECT } from '../../constant/menu/games';

const CustomTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    color: '#fff',
    boxShadow: theme.shadows[1],
    fontSize: 11,
    maxWidth: 'none',
    backgroundColor: "initial",
    textTransform: 'uppercase',
    font: 'bold 2.1vw "Helvetica Neue", Helvetica, Arial, sans-serif',
  	textShadow: '0 1px 0 #cccccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbbbbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaaaaa, 0 6px 1px rgb(0 0 0 / 10%), 0 0 5px rgb(0 0 0 / 10%), 0 1px 3px rgb(0 0 0 / 30%), 0 3px 5px rgb(0 0 0 / 20%), 0 2px 5px rgb(0 0 0 / 25%), 0 5px 5px rgb(0 0 0 / 20%), 0 10px 10px rgb(0 0 0 / 15%)',
  },
}));

const FRASES = ['Умничка!', 'Молодец!', 'Так держать!'];

const getRandomInt = (max) => {
  return Math.floor(Math.random() * max);
};

const checkAnswers = (answers, correct) => {
	let isTrue = true;
	correct.forEach(item => {
		if (!answers[item]) isTrue = false;
	});
	if (!isTrue) return false;
	let count = 0;
	for (let item in answers) {
		if (answers[item]) count++;
	}
	if (count > correct.length) return false;
	return isTrue;
};

function Excludeincorrect(props) {
	const [pointed, setPointed] = React.useState({});
	const [box, setBox] = React.useState(false);

	const handlePointClick = (id, boxId) => {
		if (boxId !== box) { 
			setPointed({ [id]: true });
			setBox(boxId);
		} else {
			if (pointed[id]) {
				setPointed({
					...pointed,
					[id]: false,
				});
			} else {
				setPointed({
					...pointed,
					[id]: true,
				});
			}
		}
	};
	return props.data.map((item, index) => {
		return (
			<div key={index} className='ex-box'>
				<div 
					style={{ fontSize: props.compareWH ? `${VH_KOEFF * 0.6}vh` : `${VW_KOEFF * 0.6}vw`	}}
					className='title-box'
				>
					{item.title}
				</div>

				{item.images.map(img => {
					return (
						<div 
							key={img.id} 
							className='game-img-box'
						>
							<CustomTooltip title={img.title} placement="bottom">
								{pointed[img.id] ? (
									<img style={{ 
											width: props.compareWH ? `${VH_KOEFF * 3}vh` : `${VW_KOEFF * 3}vw`
										}}
										className='game-img'
										src={img.isCorrect ? CORRECT : INCORRECT }
										alt={img.title}
										onClick={() => handlePointClick(img.id, item.id)}
									/>
									) : (
									<img style={{ 
											width: props.compareWH ? `${VH_KOEFF * 3}vh` : `${VW_KOEFF * 3}vw`
										}}
										className='game-img'
										src={img.src}
										alt={img.title}
										onClick={() => handlePointClick(img.id, item.id)}
									/>
								)}
							</CustomTooltip>
						</div>
					);
				})}
				{checkAnswers(pointed, item.correct) && <div style={{
					fontSize: props.compareWH ? `${VH_KOEFF * 1}vh` : `${VW_KOEFF * 1}vw`,
					color: '#fff',
					textAlign: 'center',
				}}
					>{FRASES[getRandomInt(3)]}</div>}
			</div>
		);
	});
}

export default Excludeincorrect;