import * as React from 'react';
import Button from '@mui/material/Button';
import './Puzzels.css';
import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';

//compareWH

function Puzzels(props) {
	const [pointed, setPointed] = React.useState({});
	const [answered, setAnswered] = React.useState({});

	const handlePointClick = (id) => {
		if (pointed[id]) {
			setPointed({
				...pointed,
				[id]: false,
			});
		} else {
			setPointed({
				...pointed,
				[id]: true,
			});
		}
	};

	const handleAnsweredClick = (id) => {
		if (answered[id]) {
			setAnswered({
				...answered,
				[id]: false,
			});
			setPointed({
				...pointed,
				[id]: true,
			});
		} else {
			setAnswered({
				...answered,
				[id]: true,
			});
			setPointed({
				...pointed,
				[id]: true,
			});
		}
	};

	const puzzels = props.data.map(img => {
		const coeff = pointed[img.id] ? 15 : 10;
		return (
			<div key={img.id} className={answered[img.id] ? 'puzzels-box' : 'puzzels-box puzzels-box-p'}>
				<div className='puzzels-img' onClick={() => handlePointClick(img.id)}>
					{answered[img.id] ? (
						<div 
							className='answer-data' 
							style={{ fontSize: props.compareWH ? `${VH_KOEFF * 2}vh` : `${VW_KOEFF * 2}vw` }}
							onClick={() => handleAnsweredClick(img.id)}
						>{img.alt}</div>
					) : (
						<img
							src={img.src}
							alt={img.title}
							style={{ width: props.compareWH ? `${VH_KOEFF * coeff}vh` : `${VW_KOEFF * coeff}vw` }}
						/>
					)}
				</div>
				{!answered[img.id] && (<Button variant="contained" color="success" onClick={() => handleAnsweredClick(img.id)}>
					{'ОТВЕТ'}
				</Button>)}
			</div>
		);
	});
	return (
		<div>
			{puzzels}
		</div>
	);
}

export default Puzzels;