import * as React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer';
import { MENU_0, MENU_1, MENU_2, MENU_3, MENU_4, MENU_5, MENU_6, MENU_8 } from '../constant/menu';
import Modal from './Modal';
import ListMenu from './modal/ListMenu';

function CustomMenu() {
	const [menu, setMenu] = React.useState(false);
	const [modal, setModal] = React.useState(false);

	const toggleDrawer = (anchor, open) => (event) => {
		if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
		  return;
		}
		setMenu(open);
	};


	const handleClose = () => setModal({ open: false });

	const clickMenu = (modal) => (event) => {
		setModal(modal);
	};

	const style = {
		width: 'auto',
    	height: '50px', 
    	fontSize: '50px'
	};

	const closeMenu = () => setMenu(false);
	
	return (
		<>
			<MenuIcon className='menu' onClick={toggleDrawer('left', true)} style={style} />
			<Drawer
				anchor={'left'}
				open={menu}
				onClose={toggleDrawer('left', false)}
				sx={{
					[`& .MuiDrawer-paper`]: {
						background: 'linear-gradient(235deg, #89ff00, #060c21, #00bcd4)',
						minWidth: '334px', //'34vw',
					},
				}}
			>
				{[MENU_0, MENU_1, MENU_5, MENU_2, MENU_3, MENU_6, MENU_4, MENU_8].map(menu => {
					return <ListMenu menu={menu} clickMenu={clickMenu} setModal={setModal} modal={modal} key={menu[0].id} onCloseMenu={closeMenu} />
				})}
			</Drawer>
			<Modal data={modal} handleClose={handleClose} />
		</>
	);
}

export default CustomMenu;