import * as React from 'react';
import Collapse from '@mui/material/Collapse';
import './Books.css';

function Books(props) {
	const [booked, setBooked] = React.useState({});

	const handleExpandClick = (id) => {
		if (booked[id]) {
			setBooked({
				...booked,
				[id]: false,
			});
		} else {
			setBooked({
				...booked,
				[id]: true,
			});
		}
	};

	return props.data.map(book => {
		return (
			<div className='book-box'>
				<div onClick={() => handleExpandClick(book.id)}  style={{ 
					fontSize: '26px',
				}}>
					{book.author} "{book.title}"
				</div>
				<Collapse in={booked[book.id]} timeout="auto" unmountOnExit>
					<div onClick={() => handleExpandClick(book.id)} className='book-text'>
						{book.text.map((item, index) => {
							return (
								<div key={index} style={{ 
									fontSize: '24px',
								}}>
									{item}
								</div>
							);
						})}
					</div>
				</Collapse>
			</div>
		);
	});
}

export default Books;