import * as React from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import './Gallery.css';

function srcset(image, size, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${size * cols}&h=${
      size * rows
    }&fit=crop&auto=format&dpr=2 2x`,
  };
}

const parseGalleryData = (data) => {
	return data.map(item => {
		return {
			img: item.src,
			title: item.title,
			rows: item.rows,
			cols: item.cols,
			id: item.id,
		};
	});
};

function Gallery(props) {
	const [pointed, setPointed] = React.useState({});

	const handlePointClick = (id) => {
		if (pointed[id]) {
			setPointed({
				...pointed,
				[id]: false,
			});
		} else {
			setPointed({
				...pointed,
				[id]: true,
			});
		}
	};

	return (
		<ImageList
			sx={{ width: '80vw', height: '80vh' }}
			variant="quilted"
			cols={4}
			rowHeight={121}
			style={{
				marginTop: '10vh',
				marginLeft: '15vw',
				background: 'linear-gradient(235deg, #89ff00, #060c21, #00bcd4)',
			}}
			id='imageList'
		>
			{parseGalleryData(props.data).map((item) => (
				<ImageListItem 
					key={item.img} 
					cols={pointed[item.id] ? 4 : item.cols || 1} 
					rows={pointed[item.id] ? 4 : item.rows || 1} 
					className='gallery-img'
					onClick={() => handlePointClick(item.id)}
				>
					<img
						{...srcset(item.img, 121, item.rows, item.cols)}
						alt={item.title}
						loading="lazy"
					/>
				</ImageListItem>
			))}
		</ImageList>
	);
}

export default Gallery;