import * as React from 'react';
import Button from '@mui/material/Button';
import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';

import './Mystery.css';

function Mystery(props) {
	const [answered, setAnswered] = React.useState({});

	const handleAnsweredClick = (id) => {
		if (answered[id]) {
			setAnswered({
				...answered,
				[id]: false,
			});
		} else {
			setAnswered({
				...answered,
				[id]: true,
			});
		}
	};

	const mysteries = props.data.map((item, index) => {
		return (
			<div key={item.id} className='mystery-out'>
				{answered[item.id] ? (
					<div 
						className='answer-data' 
						style={{ fontSize: props.compareWH ? `${VH_KOEFF * 1.6}vh` : `${VW_KOEFF * 1.6}vw` }}
						onClick={() => handleAnsweredClick(item.id)}
					>{item.title}</div>
				) : (
					<div className='mystery-inner' id={`box-${index}`}>
						{item.text.map((row, index) => {
							return (
								<div key={index} className='mystery-text'>{row}</div>
							);
						})}
						<Button className='mystery-btn' variant="contained" color="success" onClick={() => handleAnsweredClick(item.id)}>
							{'ОТВЕТ'}
						</Button>
					</div>
				)}
			</div>
		);
	});


	return mysteries;
}

export default Mystery;