import * as React from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import { tabsClasses } from '@mui/material/Tabs';
import TabPanel from '@mui/lab/TabPanel';
import DoneIcon from '@mui/icons-material/Done';

import { VH_KOEFF, VW_KOEFF } from '../../constant/animals';

import YoutubeEmbed from '../YoutubeEmbed';

import Mystery from './Mystery';
import IFrames from './IFrames';
import Excludeincorrect from './Excludeincorrect';

function CustomTab(props) {
	const [value, setValue] = React.useState("1");

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	const p1koeff = 0.2;
	const p2koeff = 0.2;
	const p3koeff = 0.2;
	const p4koeff = 0.2;

	//const fontSizeTitleKoeff = 0.7;
	const fontSizeTextKoeff = 0.8;
	const fontSizeIconKoeff = 0.8;
	const fontSizeTabListKoeff = 1.2;

	const buildSize = (koeff) => {
		return props.compareWH ? `${VH_KOEFF * koeff}vh` : `${VW_KOEFF * koeff}vw`;
	};

	const buildPaddingSize = () => {
		return props.compareWH ? `${VH_KOEFF * p1koeff}vh ${VH_KOEFF * p2koeff}vh ${VH_KOEFF * p3koeff}vh ${VH_KOEFF * p4koeff}vh` 
				: `${VW_KOEFF * p1koeff}vw ${VW_KOEFF * p2koeff}vw ${VW_KOEFF * p3koeff}vw ${VW_KOEFF * p4koeff}vw`;
	};

	const titles = props.data.map(tab => {
		return <Tab 
			label={tab.title} 
			value={tab.value} 
			key={tab.value} 
			wrapped 
			style={{ 
				color: '#fff', 
				fontWeight: 'bold', 
				fontSize: '15px', //buildSize(fontSizeTitleKoeff),
				border: '2px solid #fff',
				margin: '4px',
				borderRadius: '35px',
				padding: buildPaddingSize(),
				minHeight: 'auto'
			}}
		/>;
	});
	const data = props.data.map((tab, index) => {
		const contentVideo = tab.embedId && <YoutubeEmbed key={index} embedId={tab.embedId} />;
		
		const texts = tab.texts && tab.texts.map((text, index) => (
			<div 
				key={index}
				className='description' 
				style={{ fontSize: buildSize(fontSizeTextKoeff), }}
			>
				{tab.isFacts && <DoneIcon style={{ fontSize: buildSize(fontSizeIconKoeff) }}/>}
				{text}
			</div>
		));

		return (
			<TabPanel value={tab.value} key={tab.value}>
				{texts}
				{contentVideo}
				{tab.mysteries && tab.mysteries.length > 0 && <Mystery data={tab.mysteries} compareWH={props.compareWH}/>}
				{tab.iframes && tab.iframes.length > 0 && <IFrames data={tab.iframes} />}
				{tab.excludeincorrect && tab.excludeincorrect.length > 0 && <Excludeincorrect data={tab.excludeincorrect} compareWH={props.compareWH}/>}
			</TabPanel>
		);
	});

	return titles && data && <TabContext value={value}>
			<Box sx={{ borderBottom: 1, borderColor: '#fff' }}>
				<TabList 
					onChange={handleChange} 
					variant="scrollable" 
					scrollButtons
					aria-label="lab API tabs example" 
					indicatorColor="secondary" 
					textColor="secondary"
					sx={{
						[`& .${tabsClasses.scrollButtons}`]: {
							color: '#fff'
						},
						[`& .MuiSvgIcon-root`]: {
							fontSize: buildSize(fontSizeTabListKoeff),
						},
						[`& .MuiTabs-indicator`]: {
							backgroundColor: 'inherit',
						},
						[`& .Mui-selected`]: {
							border: '3px solid #fff!important',
							backgroundColor: '#00BDDB!important',
						},
					}}
				>
					{titles}
				</TabList>
			</Box>
			{data}
		</TabContext>;
}

export default CustomTab;